import React , {Component} from 'react';
import CheckoutSummary from  '../../components/Order/CheckoutSummary/CheckoutSummary';
import {Route} from 'react-router-dom';
import ContactData from './ContactData/ContactData';

class Checkout extends Component {

    state ={
        ingredients :null,
        totalPrice : 0
    }

    componentWillMount () {
        const query = new URLSearchParams(this.props.location.search);
        const ingredients ={};
        let price =0;

        for (let param of query.entries()){
            //['salad' ,'1']
            console.log(param[0]);
            if(param[0] === 'price') {
                price = param[1];
            }else{
                ingredients[param[0]] = +param[1];
            }
           
        }
        console.log(ingredients + '1111111');
        this.setState({ingredients: ingredients , totalPrice : price});
       
    }
    checkoutSummaryCancelledHandler =() =>{

        this.props.history.goBack();
    }
    checkoutSummaryContinuedHandler =() =>{
        this.props.history.replace('/checkout/contact-details');
    }
    render() {
        return  (
            <div>
                <CheckoutSummary ingredients ={this.state.ingredients}
                checkoutSummaryCancelled ={this.checkoutSummaryCancelledHandler}
                checkoutSummaryContinued={this.checkoutSummaryContinuedHandler}/>
                <Route path ={this.props.match.url + '/contact-details'} 
                render ={(props) => ( <ContactData ingredients ={this.state.ingredients} 
                price ={this.state.totalPrice} {...props}/>) }/>
            </div>
        )
    }
}

export default Checkout;


